nav {
    background-color: #0c5E3D;
    display: flex;
    justify-content: space-between;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.2);
    color: #fff;
    align-items: center;
    margin-right: -17px;
}
.logo {
    padding-left: 15px;
}

.menu > ul {
    display: flex;
    justify-content: space-between;
    padding-right: 20px;
}

.menu > ul > li {
   list-style: none;
   margin-left: 20px;
}

.style-logo {
  text-decoration: none;
  color: #fff;
  font-size: 24px;
}

.nav-link {
    color: #fff;
    text-decoration: none;

}
.nav-link:hover {
    color: rgb(163 230 53);
    border-bottom:  2px solid rgb(163 230 53); ;
}